@import url("https://fonts.googleapis.com/css?family=Libre+Baskerville|Montserrat&display=swap");

html {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

* {
  font-family: "Montserrat", serif;
}

a:hover {
  text-decoration: none;
}

.img-logo {
  width: 50%;
  height: auto;
}

.nav-link {
  font-family: "Montserrat", serif;
  color: #414141;
}

.img-profile {
  width: 80%;
  height: auto;
}

#welcome {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#welcome-header {
  font-family: "Libre Baskerville", serif;
  font-size: 4rem;
  animation: fadein 2s;
}

#subheader {
  font-family: "Libre Baskerville", serif;
  font-size: 1.5rem;
  animation: fadein 3s;
}

#scrolldown {
  position: absolute;
  bottom: 0;
  animation: fadein 3s;
  cursor: pointer;
  color: #000;
}

.img-slide {
  height: 40vh;
  width: 100%;
}

p {
  line-height: 1.75;
}

#about,
#contact {
  background-color: #f5f5f5;
  padding: 2%;
}

.about-section-row {
  margin-top: 5%;
}

.btn-cv {
  margin-top: 3%;
  padding: 3%;
  background-color: #414141;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: 300ms ease;
}

.btn-cv:hover {
  background-color: #2e2e2e;
}

.headline {
  margin-bottom: 6%;
}

.about-explanation {
  text-align: center;
}

#about-paragraph {
  line-height: 200%;
  margin-bottom: 8%;
  text-align: left;
}

.icon-skills {
  width: 75%;
  height: 7vh;
  filter: grayscale(100%);
  margin-top: 10%;
  padding-bottom: 0.4rem;
}

.icon-skills:hover {
  filter: grayscale(0%);
  transition: 0.5s;
  cursor: pointer;
}

#projects {
  margin-top: 2%;
  padding: 2%;
}

.img-portfolio {
  width: 75%;
  height: 300px;
  display: block;
  margin: 0 auto;
  overflow: hidden;
}

.img-data {
  background-color: rgba(255, 255, 255, 0.7);
  bottom: 0;
  right: 0;
  color: rgb(27, 27, 27);
  display: flex;
  opacity: 0;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  padding: 60px;
  position: absolute;
  overflow: hidden;
  transition: all 0.4s ease-in-out 0s;
}

.nospace {
  padding: 0 !important;
  margin: 0 !important;
}

.img-data:hover {
  opacity: 1;
}

.icon-link {
  color: #414141;
  margin: 0 auto;
  margin-top: 5%;
  width: 15%;
}

.social-icons {
  font-size: 4rem;
  color: #414141;
}

.social-icons:hover {
  transition: 0.5s ease;
}

#contact p {
  margin-bottom: 2%;
}

#contact {
  padding: 4%;
}

.section-break {
  background-color: transparent;
  border-top: 3px double #c8c8c8;
  height: 3px;
  margin: 1rem auto;
  width: 30%;
}

.project-break {
  background-color: transparent;
  border-top: 3px double #c8c8c8;
  height: 3px;
  margin: 1rem auto;
  width: 100%;
}

.btnLink {
  padding: 0.5em 1.5em;
  border: 0;
  background-color: #404040;
  color: #fff;
  border-radius: 5px;
  transition: 0.3s ease-in;
}

.btnLink:hover {
  background-color: hsl(0, 0%, 18%);
}

.marginRow {
  margin-bottom: 15%;
}

.separator {
  bottom: -4px;
  left: 0;
  overflow: hidden;
  position: absolute;
  width: 100%;
}

.nav-hidden {
  top: -100px;
}

.navbar {
  transition: 0.2s ease-in;
  background-color: #fff;
}

.navbar-light .navbar-toggler {
  border: none;
}

.badge-tools {
  display: flex;
  align-items: center;
}

.badge-tools > img {
  width: 15px;
  height: 15px;
}

@media only screen and (max-width: 640px) {
  .icon-skills {
    width: 25%;
    height: auto;
  }

  .div-profile {
    display: none;
  }

  .img-portfolio {
    height: 300px;
  }

  #welcome-header {
    font-size: 3.5rem;
  }

  .social-icons {
    font-size: 2.5rem;
  }

  #about-paragraph {
    text-align: left;
  }

  .colProject {
    display: none;
  }

  .separator {
    display: none;
  }

  .navbar {
    background-color: #fff;
  }
}

@media (min-width: 320px) and (max-width: 480px) {
  #welcome-header {
    font-size: 2.5rem;
  }

  #subheader {
    font-size: 1.1rem;
  }

  .img-portfolio {
    height: 200px;
  }

  #about-paragraph {
    text-align: left;
  }

  .social-icons {
    font-size: 2.5rem;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
